body {
    background: var(--black);
}
#loading.loading__active {
    opacity: 1;
    transition: all 1.6s;    
}
#loading.light {
    background: var(--light_bg);
    span {
        color: var(--black);
    }
}
#loading {
    position: fixed;
    opacity: 0;
    top: 0; left: 0;
    width: 100%;
    background: var(--dark_bg);
    height: 100%;
    z-index: 99999;
    transition: all 1.6s;
}
.loading__text {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    text-align: center;
    width: 100%;
    height: 100px;
    line-height: 100px;

    span {
        display: inline-block;
        margin: 0 5px;
        color: var(--white);
    }
    @for $i from 1 through 9{
        span:nth-child(#{$i}){
            filter: blur(0px);
            animation: blur-text 1.5s (($i)-1)*0.2s infinite linear alternate;    
        }
    }
