#footer {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    color: var(--white);
    border-top: 1px solid var(--dark_border);
    background-color: var(--dark_bg);
    display: flex;
    align-items: center;
    font-family: "Rajdhani";
    z-index: 1000;

    div {
        text-align: center;
        padding: 1.4rem;
    }
    h4,
    ul {
        display: inline-block;
    }
    h4 {
        font-weight: 600;
        padding: 0 0.6em;
        text-transform: capitalize;
    }
    a {
        position: relative;
    }
    a::before {
        content: '';
        position: absolute;
        left: 0; bottom: 0;
        width: 100%; height: 1px;
        background: var(--white);
        transform: scaleX(0);
        transition: all 0.3s ease;
    }
    a:hover::before {
        transform: scaleX(1);
        color: var(--white);      
    }

    div:nth-child(1){
        flex: 1 1 25%;
        border-right: 1px solid var(--dark_border);
    }
    div:nth-child(2){
        flex: 1 1 20%;
        border-right: 1px solid var(--dark_border);
    }
    div:nth-child(3){
        flex: 1 1 55%;

        li {
            display: inline;
            
            a {
                display: inline-block;
                padding: 0 0.6em;
                transition: all 0.3s;
                position: relative;
            }
            a::before {
                content: '';
                position: absolute;
                left: 0.6rem; bottom: 0;
                width: calc(100% - 1rem); height: 1px;
                background: var(--white);
                transform: scaleX(0);
                transition: all 0.3s ease;
            }
            a:hover::before {
                transform: scaleX(1);
                color: var(--white);    
            }
        }
    }
}

// 라이트 버전
.light#footer {
    background-color: var(--light_bg);
    border-top-color: var(--light_border);

    h4 {
        color: var(--black);
    }
    a {
        color: var(--black);
    }
    a::before {
        background: var(--black);
    }
    div {
        border-right-color: var(--light_border);

        li {
            a::before {
                background: var(--black);
            }
        }
    }
}

// 미디어 쿼리

@media (max-width: 1020px){
    #footer {
        div:nth-child(1){
            h4 {
                display: none;
            }
        }
        div:nth-child(2),
        div:nth-child(3){
            display: none;
        }        
    }
}

// 푸터 애니메이션

#footer {
    bottom: -100%;
}